body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.nav-tabs {
    width: 20%;
    background-color: rgba(0, 0, 0, 0.178);
    height: 80vh;
    overflow: auto;
    float: left;
}

.nav-link.active {
    background-color: rgba(0, 0, 0, 0.541)!important;
    color: white!important;
}

.nav-item {
    margin-top: 15%
}

.home-page-contents {
    max-width: 80%;
    color: white!important;
}
.dropdown-content {
    margin: 10px;
    width: auto;
    max-width: 90%;
}

#dropdown-menu {
    width: auto;
    background-color: rgba(128, 128, 128, 0.4);
}

#log-out-button {
    font-weight: 500;
    width: 100px;
    margin-top: 25%
}

#navbar-brand-container {
    width: 100%
}

.loading-spinner {
    margin: 0 auto;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    -webkit-animation: loading-spinner-spin 1s linear infinite;
            animation: loading-spinner-spin 1s linear infinite;
    border: 20px solid rgba(0, 255, 64, 0.3);
    border: 20px solid var(--loading-spinner-outer-color, rgba(0, 255, 64, 0.3));
    border-top: 20px solid rgb(30, 255, 0);
    border-top: 20px solid var(--loading-spinner-inner-color, rgb(30, 255, 0));
}

@-webkit-keyframes loading-spinner-spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes loading-spinner-spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

#support-form {
    width: 75%;
    margin: 0 auto;
    margin-top:10px
}

